import { useState } from "react";
import { useForm } from "react-hook-form";
// Button Components
import PrimaryButton from "@/components/Buttons/PrimaryButton";
import SecondaryButton from "@/components/Buttons/SecondaryButton";
import UtilityButton from "@/components/Buttons/UtilityButton";
import DangerButton from "@/components/Buttons/DangerButton";
// Text Components
import SecondaryHeader from "@/components/Texts/SecondaryHeader";
import TertiaryHeader from "@/components/Texts/TertiaryHeader";
import BodyText from "@/components/Texts/BodyText";
import BodyLabel from "@/components/Texts/BodyLabel";

export default function ProponentTab() {
  const [students, setStudents] = useState<any[]>([]);
  const [showModal, setShowModal] = useState(false);

  // React Hook Form setup
  const { register, handleSubmit, reset, formState: { errors } } = useForm({
    defaultValues: {
      studentId: "",
      name: "",
      grade: "",
      section: "",
      age: "",
      address: "",
      guardian: "",
      guardianContact: "",
      englishPhonemic: "",
      filipinoPhonemic: "",
      mathProficiency: ""
    }
  });

  // Add new student
  const onSubmit = (data: any) => {
    const gradeNum = Number(data.grade);
    setStudents([
      ...students,
      {
        id: Date.now(),
        ...data,
        grade: isNaN(gradeNum) ? "" : gradeNum
      }
    ]);
    reset();
    setShowModal(false);
  };

  // Delete individual student
  const handleDelete = (id: number) => {
    setStudents(students.filter(s => s.id !== id));
  };

  // Delete all students
  const handleDeleteAll = () => {
    setStudents([]);
  };

  return (
    <div>
      {/* Top Bar: Total and Actions */}
      <SecondaryHeader title="Student List Table" />
      <div className="flex flex-row justify-between items-center mb-4">
        <TertiaryHeader title={`Total: ${students.length}`} />
        <div className="flex gap-2">
          <UtilityButton small onClick={() => setShowModal(true)}>
            <span className="flex items-center gap-1">
              <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="4" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" d="M12 4v16m8-8H4"/>
              </svg>
              Add
            </span>
          </UtilityButton>
          <UtilityButton small>
            <span className="flex items-center gap-1">
              <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="3" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" d="M12 3v12" />
                <path strokeLinecap="round" strokeLinejoin="round" d="m17 8-5-5-5 5" />
                <path strokeLinecap="round" strokeLinejoin="round" d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
              </svg>
              Upload File
            </span>
          </UtilityButton>
          {students.length > 0 && (
            <DangerButton small className="bg-red-100 text-red-700 border-red-400 hover:bg-red-200" onClick={handleDeleteAll}>
              Delete All
            </DangerButton>
          )}
        </div>
      </div>

      {/* Modal for Add Student Form */}
{showModal && (
  <div className="fixed inset-0 backdrop-blur-xs flex items-center justify-center p-4 z-50">
    <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[95vh] overflow-y-auto">
      <div className="p-6">
        {/* Modal Header */}
        <div className="flex justify-between items-center mb-4 border-b pb-4">
          <SecondaryHeader title="Add New Student" />
          <button 
            onClick={() => { setShowModal(false); reset(); }}
            className="text-gray-500 hover:text-gray-700 transition-colors"
            aria-label="Close modal"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>
        
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          {/* Personal Information Section */}
          <div className="space-y-4">
            <TertiaryHeader title="Personal Information"/>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-1">
                <label className="block text-sm font-medium text-gray-700">Student ID*</label>
                <input 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm" 
                  placeholder="2023-0003" 
                  {...register("studentId", { required: true })}
                />
                {errors.studentId && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1">
                <BodyLabel title="Full Name*" />
                <input 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  placeholder="Juan Dela Cruz" 
                  {...register("name", { required: true })}
                />
                {errors.name && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1">
                <BodyLabel title="Age*"/>
                <input 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  placeholder="10" 
                  type="number"
                  min="5"
                  max="18"
                  {...register("age", { required: true, min: 5, max: 18 })}
                />
                {errors.age && <span className="text-red-500 text-xs">Required (5-18)</span>}
              </div>
            </div>
          </div>

          {/* Academic Information Section */}
          <div className="space-y-4">
            <TertiaryHeader title="Academic Information"/>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-1">
                <BodyLabel title="Grade Level*"/>
                <select 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  {...register("grade", { required: true })}
                >
                  <option value="" disabled>Select grade</option>
                  {[1, 2, 3, 4, 5, 6].map(grade => (
                    <option key={grade} value={grade}>{grade}</option>
                  ))}
                </select>
                {errors.grade && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1">
                <BodyLabel title="Section*"/>
                <select 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  {...register("section", { required: true })}
                >
                  <option value="" disabled>Select section</option>
                  {["A", "B", "C", "D", "E", "F"].map(section => (
                    <option key={section} value={section}>{section}</option>
                  ))}
                </select>
                {errors.section && <span className="text-red-500 text-xs">Required</span>}
              </div>
            </div>
          </div>

          {/* Contact Information Section */}
          <div className="space-y-4">
            <TertiaryHeader title="Contact Information" />
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-1 md:col-span-1">
                <BodyLabel title="Guardian*"/>
                <input 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  placeholder="Maria Dela Cruz" 
                  {...register("guardian", { required: true })}
                />
                {errors.guardian && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1 md:col-span-1">
                <BodyLabel title="Contact Number*"/>
                <input 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  placeholder="09123456789" 
                  type="tel"
                  {...register("guardianContact", { required: true })}
                />
                {errors.guardianContact && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1 md:col-span-3">
                <BodyLabel title="Address*"/>
                <input 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  placeholder="Brgy. Example, City, Province" 
                  {...register("address", { required: true })}
                />
                {errors.address && <span className="text-red-500 text-xs">Required</span>}
              </div>
            </div>
          </div>

          {/* Assessment Section */}
          <div className="space-y-4">
            <TertiaryHeader title="Assessment Levels"/>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-1">
                <BodyLabel title="English*"/>
                <select 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  {...register("englishPhonemic", { required: true })}
                >
                  <option value="" disabled>Select level</option>
                  <option value="-">- (Not Assessed)</option>
                  <option value="Non-Reader">Non-Reader</option>
                  <option value="Syllable">Syllable</option>
                  <option value="Word">Word</option>
                  <option value="Sentence">Sentence</option>
                  <option value="Paragraph">Paragraph</option>
                </select>
                {errors.englishPhonemic && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1">
                <BodyLabel title="Filipino*"/>
                <select 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  {...register("filipinoPhonemic", { required: true })}
                >
                  <option value="" disabled>Select level</option>
                  <option value="-">- (Not Assessed)</option>
                  <option value="Non-Reader">Non-Reader</option>
                  <option value="Syllable">Syllable</option>
                  <option value="Word">Word</option>
                  <option value="Sentence">Sentence</option>
                  <option value="Paragraph">Paragraph</option>
                </select>
                {errors.filipinoPhonemic && <span className="text-red-500 text-xs">Required</span>}
              </div>
              <div className="space-y-1">
                <BodyLabel title="Math*"/>
                <select 
                  className="w-full bg-white border border-gray-300 text-black rounded-md px-3 py-2 text-sm " 
                  {...register("mathProficiency", { required: true })}
                >
                  <option value="" disabled>Select level</option>
                  <option value="-">- (Not Assessed)</option>
                  <option value="Non-Proficient">Non-Proficient</option>
                </select>
                {errors.mathProficiency && <span className="text-red-500 text-xs">Required</span>}
              </div>
            </div>
          </div>

          {/* Form Actions */}
          <div className="flex justify-end gap-3 pt-4 border-t">
            <DangerButton 
              type="button" 
              onClick={() => { setShowModal(false); reset(); }}
              className="px-4 py-2"
            >
              Cancel
            </DangerButton>
            <PrimaryButton 
              type="submit"
              className="px-4 py-2"
            >
              Save Student
            </PrimaryButton>
          </div>
        </form>
      </div>
    </div>
  </div>
)}

      {/* Student Table Section */}
      <div className="w-full overflow-x-auto">
        <table className="min-w-full bg-white rounded-xl shadow-md">
          <thead className="text-[#013300] bg-green-50 border-b border-gray-200">
            <tr>
                <th className="px-4 py-2 text-left"><TertiaryHeader title="No#" /></th>
                <th className="px-4 py-2 text-left"><TertiaryHeader title="Student ID" /></th>
                <th className="px-4 py-2 text-left"><TertiaryHeader title="Full Name" /></th>
                <th className="px-4 py-2 text-left"><TertiaryHeader title="Grade" /></th>
                <th className="px-4 py-2 text-left"><TertiaryHeader title="Section" /></th>
                <th className="px-4 py-2 text-left"><TertiaryHeader title="Actions" /></th>
            </tr>
          </thead>
          <tbody className="text-gray-700">
            {students.map((student, idx) => (
              <tr key={student.id} className={"border-b border-gray-200 hover:bg-green-50 transition-colors duration-150"}>
                <td className="px-4 py-2">{idx + 1}</td>
                <td className="px-4 py-2">{student.studentId}</td>
                <td className="px-4 py-2">{student.name}</td>
                <td className="px-4 py-2">{typeof student.grade === "number" && !isNaN(student.grade) ? student.grade : "-"}</td>
                <td className="px-4 py-2">{student.section}</td>
                <td className="px-4 py-2 flex gap-2">
                  <UtilityButton small>
                    See All
                  </UtilityButton>
                  <DangerButton small onClick={() => handleDelete(student.id)}>
                    Delete
                  </DangerButton>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      {/* Pagination Section */}
      <div className="flex flex-row flex-wrap pt-2 items-center gap-2 text-sm sm:text-base">
        <UtilityButton small>
          <span className="flex items-center gap-1">
            <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="3" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" d="M15 19l-7-7 7-7" />
            </svg>
            Prev
          </span>
        </UtilityButton>
        <button className="text-gray-500 font-semibold px-3 py-1.5 rounded bg-gray-100 cursor-default" disabled>
          1
        </button>
        <UtilityButton small>
          <span className="flex items-center gap-1">
            Next
            <svg className="w-4 h-4" fill="none" stroke="currentColor" strokeWidth="3" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" d="M9 5l7 7-7 7" />
            </svg>
          </span>
        </UtilityButton>
      </div>
    </div>
  );
}